<template>
	<view class="background fix-padding">
		<view class="details_list">
			<view class="details_item flex_between">
				<view>
					<view class="details_item_list">
						<view class="title">客户姓名</view>
						<view class="value">{{form.name}}</view>
					</view>
					<view class="details_item_list">
						<view class="title">客户地址</view>
						<view class="value">{{form.address}}</view>
					</view>
					<view class="details_item_list">
						<view class="title">联系方式1</view>
						<view class="value">{{form.mobile_one}}</view>
					</view>
					<view class="details_item_list">
						<view class="title">联系方式2</view>
						<view class="value">{{form.mobile_two}}</view>
					</view>
					<view class="details_item_list">
						<view class="title title_style">是否要排尺</view>
						<view class="value" v-if="form.is_rowruler == 1">是</view>
						<view class="value" v-if="form.is_rowruler == 2">否</view>
					</view>
					<view class="details_item_list">
						<view class="title">订货日期</view>
						<view class="value">{{form.order_date}}</view>
					</view>
					<view class="details_item_list">
						<view class="title">送货日期</view>
						<view class="value">{{form.delivery_date}}</view>
					</view>
					<view class="details_item_list">
						<view class="title title_style">是否含辅料</view>
						<view class="value" v-if="form.is_accessories == 1">是</view>
						<view class="value" v-if="form.is_accessories == 2">否</view>
					</view>
					<view class="details_item_list">
						<view class="title">备注说明</view>
						<view class="value">{{form.remarks}}</view>
					</view>
				</view>
			</view>
		</view>
		<view class="details_list">
			<view v-for="item in form.goods_data" class="details_item flex_between">
				<view>
					<view class="details_item_list">
						<view class="title">商品名称</view>
						<view class="value">{{item.name}}</view>
					</view>
					<view class="details_item_list">
						<view class="title">商品数量</view>
						<view class="value">{{item.goods_quantity}}</view>
					</view>
					<view class="details_item_list">
						<view class="title">商品单价</view>
						<view class="value">￥{{item.price}}</view>
					</view>
					<view class="details_item_list">
						<view class="title">商品总价</view>
						<view class="value">￥{{item.total_price}}</view>
					</view>
				</view>
			</view>
		</view>
		<view class="details_list">
			<view v-for="item in form.workmanship_data" class="details_item flex_between">
				<view>
					<view class="details_item_list">
						<view class="title">工艺项目</view>
						<view class="value">{{item.project_name}}</view>
					</view>
					<view class="details_item_list">
						<view class="title">工艺数量</view>
						<view class="value">{{item.quantity}}</view>
					</view>
					<view class="details_item_list">
						<view class="title">工艺单价</view>
						<view class="value">￥{{item.workmanship_price}}</view>
					</view>
					<view class="details_item_list">
						<view class="title">工艺总价</view>
						<view class="value">￥{{item.workmanship_sub_total}}</view>
					</view>
				</view>
			</view>
		</view>
		<view class="details_note">
			<view class="title">排尺图片</view>
			<view class="remarkImage flex">
				<view class="item_image" @click="uploadImage">
					<image class="image" src="/static/image/pic.png"></image>
				</view>
				<view class="item_image" v-for="(item,index) in remarkImage" :key='index'>
					<image class="image" :src="item" @click="previewImage(index)" mode="aspectFill"></image>
					<image class="delete" src="/static/image/delete.png" @click="deleteImage"></image>
				</view>
			</view>
		</view>
		<view class="container fix-bottom">
		    <button class="btn-submit" @click="submit">提交</button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				id:'',
				form:[],
				remarkImage: [],
			}
		},
		onLoad(option) {
			console.log('option',option)
			if(option.data) {
				this.id = JSON.parse(decodeURIComponent(option.data))
				this.listInterface()
			}
			
		},
		methods: {
			// 添加图片
			uploadImage() {
				 if (this.remarkImage.length == 8) {
					uni.showToast({
						title: "最多可以上传8张图片",
						icon: "none"
					})
					return
				}
				uni.chooseImage({
					count: 1,
					success: res => {	
						let filePath = res.tempFilePaths[0];
						uni.uploadFile({
							url: this.common.baseUrl + this.api.upload,
							filePath: filePath,
							name: "file",
							success: res => {
								let data = JSON.parse(res.data);
								if (data.code == 1) {
									console.log('data.data',data.data)
									this.remarkImage.push(data.data.url)
								} else {
									uni.showToast({
										title: "上传失败",
										icon: "none"
									})
								}
							},
							fail: res => {
								uni.showToast({
									title: "上传失败," + res,
									icon: "none"
								})
							}
						})
					}
				})
			},
			// 图片放大
			previewImage(i) {
				wx.previewImage({
					current: this.remarkImage[i],
					urls: this.remarkImage
				})
			},
			// 删除图片
			deleteImage(i) {
				this.remarkImage.splice(i, 1)
			},
			// 接口列表
			listInterface() {
				this.http({
				    url: this.api.order_info,
				    method: "POST",
				    data: {
				        uid: this.common.get("uid"),
				        id:this.id
				    },
				    success: res => {
				        this.form = res
						this.remarkImage = res.amount_image
				    }
				})
			},
			// 返回上一页
			previous() {
				let pages = getCurrentPages(); // 当前页面
				let beforePage = pages[pages.length - 2]; // 上一页
				uni.navigateBack({
				    success: function() {
				        beforePage.onLoad(); // 执行上一页的onLoad方法
				    }
				});
			},
			submit() {
				uni.showModal({
					title: '提示',
					content: '确定当前操作吗？',
					success: res => {
						if(res.confirm) { 
							this.http({
							    url: this.api.up_order_img,
							    method: "POST",
							    data: {
							        uid: this.common.get("uid"),
							        id:this.id,
									amount_image:this.remarkImage
							    },
							    success: res => {
							        uni.showToast({
							        	title:'编辑成功',
							        	icon:'none',
							        	duration: 2000
							        })
							        setTimeout(() =>{
							        	this.previous()
							        },500) 
							    }
							})
						}
					},
					fail: () => {},
					complete: () => {}
				});			
				
			}
		}
	}
</script>

<style lang="scss">
.background {
	// background: #F6F6F6;
}

.details_list {
	// margin-bottom: 20rpx;
	border-bottom: 20rpx solid #F6F6F6;
	.details_item {
		display: flex;
		align-items: flex-start;
		width: 100%;
		padding: 20rpx 30rpx;
		background: $color-f;
		 box-sizing: border-box;
		 border-bottom: 2rpx solid #ECECEC;
		.details_item_list {
			display: flex;
			flex-direction: row;
			font-size: 28rpx;
			height: 50rpx;
			line-height: 50rpx;
			margin-bottom: 20rpx;
			.title {
				width: 130rpx;
				font-weight: 400;
				color: $color-6;
				margin-right: 40rpx;
				text-align-last: justify;
			}
			.title_style {
				width: 142rpx;
			}
			.value {
				font-weight: 500;
				color: $color-3;
			}
		}
	}
}
.details_note {
	background: $color-f;
	padding: 20rpx 30rpx;
	display: flex;
	flex-direction: row;
	font-size: 28rpx;
	.title {
		margin-top: 14rpx;
		width: 120rpx;
		font-weight: 400;
		color: $color-6;
	}
	.value {
		font-weight: 500;
		color: $color-3;
	}
	.remarkImage {
	    width: 558rpx;
	    margin-left: 20rpx;
	    flex-wrap: wrap;
		background: $color-f;
	    .item_image {
	        width: 160rpx;
			height: 160rpx;
	        margin-right: 10rpx;
	        margin-top: 10rpx;
			background: $color-f;
			position: relative;
	        .image {
				display: block;
	            width: 160rpx;
	            height: 160rpx;
	            border-radius: 10rpx;
	            object-fit: cover;
	        }
			.delete {
				display: block;
	            position: absolute;
	            width: 36rpx;
	            height: 36rpx;
	            padding: 10rpx;
	            top: -5rpx;
	            right: -5rpx;
	        }
	    }
	}
}

</style>
